body {
    background-color: #f1faee;
}

@font-face {
  font-family: 'Gotham';
  src: local('Gotham'), url(./fonts/iCiel Gotham-Medium.ttf) format('truetype');
}

nav {
    /* border-bottom: 4px solid #D9E5D5; */
    display: flex;
    justify-content: space-around;
    align-items: center;
    min-height: 10vh;
    padding: 0px 0px;
    background: inherit;
    color: black;
}

.nav-links {
    width:50%;
    display:flex;
    justify-content: space-around;
    align-items: center;
    list-style: none;
    font-family: "helvetica";
}

.banner-1 {
    margin-left: 100px;
    margin-top: 100px;
    font-size: 100px;
    text-align: left;
}
.revealComp-1 {
    font-family:"Gotham";
    color:#457b9d;
}

.revealComp-2 {
    font-family:"Gotham";
    color:#e63946;
}

.comp-2 {
    color: #1d3557;
    margin-top: 100px;
    padding: 50px 100px;
    font-size:20px;
    animation: text 3s 1;
}

@keyframes text {
    0%{
        color:#f1faee;
        margin-left: -20px;
    }
}

.about-page {
    color: #1d3557;
    padding: 50px 50px;
    animation: text 3s 1;
}

.about-page h1 {
    font-size: 30px;
}

.blog-post {
    color: #1d3557;
    animation: text2 1s 1;
    text-align: center;
}

.blog-post a {
    float:left;
    display:inline-block;
    margin-left: 20px;
    margin-bottom: 20px;
    text-decoration: none;
    color: inherit;
    width: 50%;
}

.blog-content {
    width:50%;
    display: inline-block;
    text-align: left;
    padding: 0px 50px;
}


@keyframes text2 {
    0%{
        color:#f1faee;
        margin-top: -10px;
    }
}

.service-post {
    color: #1d3557;
    padding: 10px 50px;
    animation: text2 1s 1;
}

.servie-post h1 {
    padding: 0px 20px;
}

.service-post p {
    padding: 0vw 5vw;
}

@media only screen and (max-width: 768px) {
    .banner-1 {
        margin-left: 0px;
        margin-top: 0px;
        padding: 20px 40px;
        font-size: 60px;
    }
    .comp-2 {
        margin-top: 0px;
        font-size: 15px;
        padding: 50px 25px;
    }

    .blog-post h2 {
        padding: 0px 10px;
    }
    .blog-post a {
        width: 100%;
        float:left;
        text-align: left;
    }
    .blog-content {
        width: 90%;
        padding: 0px 0px;
        line-height: 2.0;
    }
    .blog-content p {
        padding: 10px 0px;
    }

    .service-post {
        color: #1d3557;
        padding: 10px 20px;
        animation: text2 2s 1;
    }
    .servie-post h1 {
        padding: 0px 10px;
    }

    .service-post p {
        padding: 0vw 0vw;
        line-height: 1.8;
    }
}

